.form {
  position: relative;
  display: flex;
  align-items: center;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-bottom: 12px;
    font-size: 16px;
  }

  @media (orientation: portrait) {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  flex: none;
  margin-right: 3rem;
  padding: 0 5.6rem;
  height: 6rem;
  width: calc(100% - 21rem);
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-dark;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transition-property: width, padding;
  transition-duration: 0.7s, 0.7s;
  transition-timing-function: ease-in-out, ease-in-out;
  margin-left: auto;

  &:focus {
    border: 1px solid $c-purple;
  }

  @media (min-width: $stop-scaling) {
    margin-right: 30px;
    padding: 0 56px;
    height: 60px;
    width: calc(100% - 210px);
    font-size: 24px;
    border-radius: 30px;
  }

  @media (orientation: portrait) {
    margin-right: 1rem;
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    width: calc(100% - 9rem);
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin-right: 1rem;
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }

  .screen.screen--game:not(.active) & {
    padding: 0;
    width: 0;
  }
}

.form__button {
  @media (orientation: portrait) {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 4rem;
    height: 4rem;
  }

  svg {
    @include center;
    display: none;
    width: 1.6rem;
    height: 1.8rem;

    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 18px;
    }

    @media (orientation: portrait) {
      display: block;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      display: block;
    }
  }
}

.form--result {
  .form__field {
    --result-width: calc(100% - 31rem);

    width: 0;
    animation-name: show-input-field;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-timing-function: ease-in-out;
    animation-delay: 2.3s;

    &::placeholder {
      opacity: 0;
      transition: opacity 0.3s ease 2.8s;

      .screen--show & {
        opacity: 1;
      }
    }

    @media (min-width: $stop-scaling) {
      --result-width: calc(100% - 310px);
    }

    @media (orientation: portrait) {
      --result-width: calc(100% - 5rem);
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      --result-width: calc(100% - 9rem);
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      --result-width: calc(100% - 5rem);
    }
  }

  .form__button {
    animation-name: show-input-button;
    animation-duration: 0.4s;
    animation-delay: 2s;
    animation-fill-mode: both;
    animation-timing-function: ease;
  }
}

@keyframes show-input-field {
  0% {
    opacity: 0;
    width: 0;
  }

  10% {
    opacity: 1;
  }

  100% {
    width: var(--result-width);
  }
}

@keyframes show-input-button {
  0% {
    opacity: 0;
    color: transparent;
  }

  40% {
    opacity: 1;
    color: transparent;
  }

  100% {
    color: $c-dark;
  }
}
