@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translate3d(0, 1em, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes dash-in {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes dash-out {
  to {
    stroke-dashoffset: var(--dashoffset-length);
  }
}
.animation-screen {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.animation-text__word {
  display: inline-block;
  padding: 5px 0 0;
  overflow: hidden;
}
.animation-text__word > span {
  display: inline-block;
  transform: translate3d(0, 1.2em, 0);
  will-change: transform;
}

.screen.active .animation-text__word > span {
  transform: translate3d(0, 0, 0);
}

.intro__title > .animation-text__word {
  letter-spacing: -8px;
}
.intro__title > .animation-text__word:first-child {
  padding: 26px 0 0;
}

.intro__date.active-animation > .animation-text__word > span.space {
  width: 10px;
}

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 1.5rem 2.9rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3rem;
  color: #5f458c;
  letter-spacing: -0.04em;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
  z-index: -1;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.7rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.9rem 0;
  width: 100%;
  max-height: calc(100vh - 33.9rem);
  max-width: 79.8rem;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    padding: 0 59px 0;
    max-height: calc(100vh - 339px);
    max-width: 798px;
  }
}
@media (orientation: portrait) {
  .chat__body {
    padding: 0 0 0;
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 25rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__body {
    padding: 0;
    max-height: calc(var(--vh, 1vh) * 100 - 16rem);
  }
}

.chat__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.chat__message {
  flex: none;
  display: flex;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .chat__message {
    margin-bottom: 20px;
    font-size: 24px;
  }
}
@media (orientation: portrait) {
  .chat__message {
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message {
    font-size: 1.6rem;
  }
}
.chat__message p {
  margin: 0;
  padding: 1.6rem 5.7rem;
  min-width: 17rem;
  border-radius: 4rem;
}
@media (min-width: 2000px) {
  .chat__message p {
    padding: 16px 57px;
    min-width: 170px;
    border-radius: 40px;
  }
}
@media (orientation: portrait) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
.chat__message--outcoming {
  justify-content: flex-start;
}
.chat__message--outcoming p {
  width: 100%;
  color: #5f458c;
  background-color: #ffffff;
  word-break: break-word;
}
.chat__message--incoming {
  justify-content: flex-end;
  text-align: center;
}
.chat__message--incoming p {
  color: #ffffff;
  background-color: #a67ee5;
  word-break: break-word;
}

.chat__placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17rem;
  height: 6rem;
  background-color: #a67ee5;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .chat__placeholder {
    width: 170px;
    height: 60px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
.chat__placeholder span {
  margin: 0 0.65rem;
  width: 1rem;
  height: 1rem;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .chat__placeholder span {
    margin: 0 6.5px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
.chat__placeholder--hidden {
  opacity: 0;
}

.chat__footer {
  margin-top: auto;
}

.disclaimer {
  color: #5f458c;
  background-color: #ffffff;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
.disclaimer ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.2rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .disclaimer ul {
    font-size: 12px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
.disclaimer li {
  position: relative;
  padding: 0 0.6rem;
}
@media (min-width: 2000px) {
  .disclaimer li {
    padding: 0 6px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
.disclaimer li::before {
  content: "/";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-50%, -50%);
}
.disclaimer li:first-child::before {
  display: none;
}
.disclaimer a {
  display: inline-block;
  vertical-align: top;
  padding: 0.9rem 0 0.7rem;
  text-decoration: underline;
  color: inherit;
}
.disclaimer a:hover {
  opacity: 0.8;
}
.disclaimer a:focus {
  outline: none;
  opacity: 0.7;
}
@media (min-width: 2000px) {
  .disclaimer a {
    padding: 9px 0 7px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}

.form {
  position: relative;
  display: flex;
  align-items: center;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 2000px) {
  .form__label {
    margin-bottom: 12px;
    font-size: 16px;
  }
}
@media (orientation: portrait) {
  .form__label {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  flex: none;
  margin-right: 3rem;
  padding: 0 5.6rem;
  height: 6rem;
  width: calc(100% - 21rem);
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transition-property: width, padding;
  transition-duration: 0.7s, 0.7s;
  transition-timing-function: ease-in-out, ease-in-out;
  margin-left: auto;
}
.form__field:focus {
  border: 1px solid #a67ee5;
}
@media (min-width: 2000px) {
  .form__field {
    margin-right: 30px;
    padding: 0 56px;
    height: 60px;
    width: calc(100% - 210px);
    font-size: 24px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .form__field {
    margin-right: 1rem;
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__field {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field {
    margin-right: 1rem;
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }
}
.screen.screen--game:not(.active) .form__field {
  padding: 0;
  width: 0;
}

@media (orientation: portrait) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__button {
    width: 4rem;
    height: 4rem;
  }
}
.form__button svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 1.6rem;
  height: 1.8rem;
}
@media (min-width: 2000px) {
  .form__button svg {
    width: 16px;
    height: 18px;
  }
}
@media (orientation: portrait) {
  .form__button svg {
    display: block;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button svg {
    display: block;
  }
}

.form--result .form__field {
  --result-width: calc(100% - 31rem);
  width: 0;
  animation-name: show-input-field;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-timing-function: ease-in-out;
  animation-delay: 2.3s;
}
.form--result .form__field::placeholder {
  opacity: 0;
  transition: opacity 0.3s ease 2.8s;
}
.screen--show .form--result .form__field::placeholder {
  opacity: 1;
}
@media (min-width: 2000px) {
  .form--result .form__field {
    --result-width: calc(100% - 310px);
  }
}
@media (orientation: portrait) {
  .form--result .form__field {
    --result-width: calc(100% - 5rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form--result .form__field {
    --result-width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form--result .form__field {
    --result-width: calc(100% - 5rem);
  }
}
.form--result .form__button {
  animation-name: show-input-button;
  animation-duration: 0.4s;
  animation-delay: 2s;
  animation-fill-mode: both;
  animation-timing-function: ease;
}

@keyframes show-input-field {
  0% {
    opacity: 0;
    width: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    width: var(--result-width);
  }
}
@keyframes show-input-button {
  0% {
    opacity: 0;
    color: transparent;
  }
  40% {
    opacity: 1;
    color: transparent;
  }
  100% {
    color: #5f458c;
  }
}
.game {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 14.6rem 0 6rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .game {
    padding: 146px 0 60px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game {
    padding: 5.9rem 0 4rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game {
    padding: 9rem 0 6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game {
    padding: 5rem 0 1.5rem;
  }
}

.game__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.1rem;
  padding-right: 0.6rem;
}
@media (min-width: 2000px) {
  .game__header {
    margin-bottom: 11px;
    padding-left: 6px;
  }
}
@media (orientation: portrait) {
  .game__header {
    margin-bottom: 1.5rem;
    padding-right: 0;
  }
}

.game__title {
  margin: 0;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
}
@media (min-width: 2000px) {
  .game__title {
    font-size: 48px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__title {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__title {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__title {
    font-size: 2rem;
