.game__counter {
  margin: 0;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: 0;
}
@media (min-width: 2000px) {
  .game__counter {
    font-size: 48px;
  }
}
@media (min-width: 1024px) {
  .game__counter {
    margin-right: 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__counter {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__counter {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__counter {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.game__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.game__buttons {
  position: fixed;
  left: 0;
  z-index: 100;
  opacity: 0.5;
}

.game__button {
  margin: 0 30px 30px;
  display: block;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 15.5rem 0 10rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .intro {
    padding: 155px 0 100px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro {
    padding: 6rem 0 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0 5vh;
  }
}

.intro__title {
  margin: 0;
  max-width: 99rem;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
}
@media (min-width: 2000px) {
  .intro__title {
    max-width: 990px;
    font-size: 120px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__title {
    max-width: 100%;
    font-size: 3.6rem;
  }
