@keyframes show-suitcase {
  0% {
    transform: translateY(70px) scale(0);
  }
  50% {
    transform: translateY(-300px) scale(1.2);
  }
  100% {
    transform: translateY(-250px) scale(1);
  }
}
@keyframes show-flamingo {
  0% {
    opacity: 0;
    transform: translateY(70px);
  }
  100% {
    opacity: 1;
    transform: translateY(-250px);
  }
}
@keyframes float-flamingo {
  0% {
    transform: rotate(1deg) translate(0, -250px);
  }
  100% {
    transform: rotate(-2deg) translate(-4px, -220px);
  }
}
@keyframes show-leaf-blue-1 {
  0% {
    opacity: 0;
    transform: rotate(-20deg);
  }
  20% {
    transform: rotate(20deg);
  }
  40% {
    transform: rotate(-15deg);
  }
  60% {
    opacity: 1;
    transform: rotate(10deg);
  }
  80% {
    transform: rotate(-5deg);
  }
  100% {
    transform: none;
  }
}
@keyframes show-leaf-blue-2 {
  0% {
    opacity: 0;
    transform: rotate(25deg);
  }
  20% {
    transform: rotate(-20deg);
  }
  40% {
    transform: rotate(15deg);
  }
  60% {
    opacity: 1;
    transform: rotate(-10deg);
  }
  80% {
    transform: rotate(5deg);
  }
  100% {
    transform: none;
  }
}
@keyframes show-leaf-green {
  0% {
    opacity: 0;
    transform: rotate(-15deg);
  }
  20% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(-5deg);
  }
  60% {
    opacity: 1;
    transform: rotate(3deg);
  }
  80% {
    transform: rotate(-1deg);
  }
  100% {
    transform: none;
  }
}
@keyframes show-result-button {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes roll-in-result-button {
  0% {
    transform: translateX(50px) rotate(180deg);
  }
  100% {
    transform: none;
  }
}
